footer.footer-container {
  display: flex;
  justify-content: space-between;
  padding: 0 5%;
  flex-wrap: wrap;
  height: 200px;
  max-width: 1400px;
  margin: 50px auto;

  &::before {
    content: "";
    background: url(../images/bg-image.webp);
    background-repeat: repeat;
    background-size: auto;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    background-size: 100% 100%;

    height: 300px;
    opacity: 0.2;

    bottom: -100px;
    left: 0;
    right: 0;
    z-index: -1;
  }

  a {
    @include gradient;
    text-decoration: none;
    cursor: pointer;
  }
  & > div {
    height: 100%;
    display: flex;
    align-items: center;
  }
  .column-2 {
    display: flex;
    justify-content: end;

    .main-header__social-links {
      display: flex;
      flex-wrap: wrap;
      padding-left: 0px;
      list-style: none;
      max-width: 160px;
      li {
        width: 100%;
        display: block;

        a {
          display: flex;
          align-items: center;
          height: 100%;
        }
        &:nth-child(1) {
          max-width: 70px;
        }
        &:nth-child(2) {
          max-width: 40px;
        }
        &:nth-child(3),
        &:nth-child(4) {
          margin-bottom: 15px;
          a {
            font-size: 20px;
            text-decoration: none;
          }
        }

        &:nth-child(1),
        &:nth-child(2) {
          flex-basis: 50%;
          width: 100%;
        }
      }
    }
  }
}
